.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1;
  background: #0003;
  transition: all .25s cubic-bezier(.4, 0, .2, 1), all .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 100%;
  height: 502px;
  filter: drop-shadow(-8px 0 20px #00000040);
  background: #e17992;
  padding-top: 23px;
  padding-right: 28px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1), all .4s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header__close {
  width: 14px;
  height: 14px;
  background-color: #0000;
  border: none;
  margin-left: auto;
  padding: 0;
  display: block;
}

