body {
  font-family: 'DM Sans';
}

html{
  scroll-behavior: smooth;
  scroll-padding-top: 70px;
}

.container {
  // outline: 2px solid red;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
  

}

button {
  cursor: pointer;
}
